$color-primary: #107895;
.cart_item {
  align-content: center;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 2rem;
  width: 100rem;
  max-width: 100%;
  &:not(:last-child) {
    border-bottom: 0.2rem solid $color-primary;
  }
}
.cart_item-left {
  align-items: center;
  display: flex;
}
.cart_item-right {
  align-items: center;
  display: flex;
}
.cart_item-title {
  font-size: 3rem;
  margin: 0.5rem 1rem;
}
.cart_item-author {
  font-size: 1.8rem;
  margin-left: 2rem;
}
.cart_item-price {
  font-size: 2rem;
  margin-left: 2rem;
}
.cart_item-img {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 100%;
  border-radius: 0.2rem;
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 0.5rem 0.5rem rgba(0, 0, 0, 0.22);
  height: 20rem;
  width: 15rem;
}
.cart_item-quantity {
  font-size: 2.5rem;
  margin: 1rem;
  text-align: center;
  width: 3.2rem;
}
.cart_item-button {
  background-color: $color-primary;
  border: none;
  border-radius: 0.5rem;
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 0.5rem 0.5rem rgba(0, 0, 0, 0.22);
  color: white;
  cursor: pointer;
  font-size: 2.5rem;
  height: 3rem;
  margin: 0.7rem;
  outline: none;
  width: 3rem;
  &:hover {
    background-color: darken($color-primary, 10);
  }
  &:active {
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
    transform: translateY(-0.2rem);
  }
}
.cart_item-remove {
  background-color: rgb(207, 27, 27);
  border: none;
  border-radius: 0.7rem;
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 0.5rem 0.5rem rgba(0, 0, 0, 0.22);
  color: white;
  cursor: pointer;
  font-size: 1.5rem;
  height: 4rem;
  margin: 1rem;
  outline: none;
  width: 12rem;
  &:hover {
    background-color: rgb(145, 2, 2);
  }
  &:active {
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
    transform: translateY(-0.2rem);
  }
}
@media (max-width: 600px) {
  .cart_item-right {
    align-items: center;
    display: flex;
    flex-direction: column;
  }
  .cart_item-img {
    background-size: 150%;
    border-radius: 0.2rem;
    height: 18rem;
    width: 12rem;
  }
  .cart_item-title {
    font-size: 2rem;
  }
  .cart_item-author {
    font-size: 1.2rem;
    margin-left: 2rem;
  }
  .cart_item-button {
    margin: 0.5rem;
  }
  .cart_item-quantity {
    margin: 0.5rem;
  }
}
